@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lora&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.ScrollAnimation-Title-Container{padding:10px;border-radius:20px;max-width:300px;text-align:center;margin:20px;position:fixed;margin-left:25%}@media screen and (max-width: 500px){.ScrollAnimation-Title-Container{margin-left:10%}}.ScrollAnimation-Title-Border{border:1px #000 solid;border-radius:10px;padding:10px 20px}
.ScrollAnimation-CenteredTitle-Container{padding:10px;border-radius:20px;max-width:300px;text-align:center;margin:20px;position:fixed;margin-left:calc(50% - (300px / 2))}.ScrollAnimation-CenteredTitle-Border{border:1px #000 solid;border-radius:10px;padding:10px 20px}
.ScrollAnimation-Attribution-Container{padding:10px;border-radius:20px;max-width:300px;text-align:center;margin:20px;position:fixed;margin-left:25%}@media screen and (max-width: 500px){.ScrollAnimation-Attribution-Container{margin-left:10%}}.ScrollAnimation-Attribution-Border{border:1px #000 solid;border-radius:10px;padding:10px 20px;white-space:pre-line}
.ScrollAnimation-CenteredAttribution-Container{padding:10px;border-radius:20px;max-width:300px;text-align:center;margin:20px;position:fixed;margin-left:calc(50% - (300px / 2))}.ScrollAnimation-CenteredAttribution-Border{border:1px #000 solid;border-radius:10px;padding:30px 40px;font-size:1.2rem;white-space:pre-line}.ScrollAnimation-CenteredAttribution-Border .Back-Button{display:block;padding-top:20px}
.ScrollAnimation-Stanza-Container{padding:30px 40px;border-radius:20px;max-width:300px;text-align:center;font-size:1.2rem;margin:20px;position:fixed;left:25%;z-index:2;opacity:.95;white-space:pre-line}@media screen and (max-width: 500px){.ScrollAnimation-Stanza-Container{left:10%}}
.ScrollAnimation-StanzaImage-Container{padding:10px 20px;border-radius:20px;max-width:300px;min-width:300px;text-align:center;margin:20px;position:fixed}
.Form-Container{background-color:#f9e9cf;color:#000}.Form-Container button{cursor:pointer}.Form{height:calc(100vh - 59px);overflow:scroll;padding:20px}.disclaimer{padding-bottom:40px}.Save-Button-Container{display:block;width:280px;padding-bottom:10px;margin:0 auto;border-top:1px dotted #f0f8ff}.Save-Button{width:72px;margin:0 10px}.Green-Button{background-color:skyblue;border-radius:3px;border:1px solid blue}.Green-Button:hover{background-color:#5bbce4}.Form-Stanza-Container,.Form-Image-Container{margin:10px 0;border:1px #625ace solid;padding:4px;border-radius:4px;position:relative}.Form-Stanza-Container textarea,.Form-Image-Container textarea{max-width:100%}.Form-Stanza-Container div.Label-Input-Group,.Form-Image-Container div.Label-Input-Group{display:flex;justify-content:space-between;margin-top:2px}.Form-Stanza-Container div.Label-Input-Group input,.Form-Image-Container div.Label-Input-Group input{width:80px}.Form-Stanza-Container div.Label-Input-Group select,.Form-Image-Container div.Label-Input-Group select{width:130px}.Form-Stanza-Container div.Label-Input-Group.hidden,.Form-Image-Container div.Label-Input-Group.hidden{display:none}.Form-Stanza-Container div.Label-Input-Group.First,.Form-Image-Container div.Label-Input-Group.First{margin-top:5px}.Form-Stanza-Container div.Background-Label-Input,.Form-Image-Container div.Background-Label-Input{margin-top:10px;display:flex;justify-content:space-between}.Form-Stanza-Container div.Background-Label-Input input,.Form-Image-Container div.Background-Label-Input input{width:80px}#UrlInput{width:200px}.Title-Input-Group{display:flex;justify-content:space-between;margin-top:2px}.Default-Option{display:flex;justify-content:space-between;margin-right:20px;margin-top:2px}.Form-Image-Container .secondary{margin-top:-10px}.secondary{background-color:red;border:none;border-radius:3px;color:#fff;position:absolute;top:0;right:0}.secondary:hover{background-color:#c00}.File-Upload{margin-left:10px}.blue{color:purple}#HorizontalRangeSlider{-webkit-transform:rotate(180deg);transform:rotate(180deg)}
.Content-Wrap{height:100vh}.bm-burger-button{position:fixed;width:36px;height:30px;left:36px;top:36px}.bm-burger-bars{background:#373a47}.bm-burger-bars-hover{background:#a90000}.bm-cross-button{height:24px;width:24px}.bm-cross{background:#000}.bm-menu-wrap{position:fixed;height:100%}.bm-menu{background:#ddd;font-size:1.15em}.bm-morph-shape{fill:#373a47}.bm-item{display:inline-block}.bm-overlay{background:rgba(0,0,0,.3)}#page{display:inline-block;width:100%;margin-bottom:-5px}
